<template>
  <div class="">
    <!-- <button @click="showMap = !showMap">Скрыть</button> -->
    <l-map
      v-if="this.coordinats == 0"
      style="height: 500px"
      :zoom="zoom"
      :center="center"
    >
      <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
    </l-map>
    <div class="" v-if="this.coordinats != 0">
      <l-map style="height: 500px" :zoom="zoom" :center="coordinats">
        <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
        <l-marker :lat-lng="this.coordinats" :icon="icon"></l-marker>
      </l-map>
    </div>
  </div>
</template>

<script>
import { LMap, LTileLayer, LMarker } from 'vue2-leaflet';
import { icon } from 'leaflet';
import { mapState } from 'vuex';

export default {
  components: {
    LMap,
    LTileLayer,
    LMarker,
  },
  data() {
    return {
      showMap: true,
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution:
        '&copy; <a target="_blank" href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      zoom: 15,

      center: [54.965008, 82.94634],
      markerLatLng: [54.965008, 82.94634],
      icon: icon({
        iconUrl: require('../assets/img/icon2.png'),
        iconSize: [40, 40],
        iconAnchor: [16, 37],
      }),
    };
  },
  computed: {
    ...mapState({
      coordinats: (state) => state.coordinats.coordinats,
    }),
  },
};
</script>
