import axios from '@/api/axios';

const allDevices = () => {
  return axios.get('/info/all/unit');
};
const postCommand = (credentials) => {
  return axios.post('/info/send/message', credentials);
};
const postSearch = (credentials) => {
  return axios.get('/info/search/block', {
    params: {
      serial: credentials.serial,
      phone: credentials.phone,
      imeiBlock: credentials.imeiBlock,
    },
  });
};
export default {
  allDevices,
  postCommand,
  postSearch,
};
