<template>
  <!-- @click="pop" -->
  <tr @contextmenu.prevent="pop">
    <td><span>П/п</span>{{ this.$attrs['idx'] + 1 }}</td>
    <td><span>Серийный номер</span>{{ this.item_data.serial }}</td>
    <td><span>Номер Сим-карты</span>{{ this.item_data.sim_serial }}</td>
    <td><span>IMEI устройства</span> {{ this.item_data.imei_block }}</td>
    <td><span>IMEI СИМ-карты</span> {{ this.item_data.imei_sim }}</td>
    <td><span>Пароль</span>{{ this.item_data.password }}</td>
  </tr>
</template>

<script>
export default {
  name: 'AppTableDeviceItem',
  data() {
    return {};
  },
  props: {
    item_data: {
      type: Object,
      default() {
        return {};
      },
      idx: {
        type: Number,
        default() {
          return {};
        },
      },
    },
  },
  methods: {
    pop() {
      this.$emit('modal-mini', this.item_data);
      this.showModal = true;
    },
  },
};
</script>

