<template>
  <transition name="modal">
    <div class="modal-mask" @click="$emit('close')">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="modal-body">
            <input
              type="button"
              value="Отправить команду"
              class="modal__btn"
              @click="createNewCommand()"
            />
            <input
              type="button"
              value="Показать на карте"
              class="modal__btn"
              @click="submitCoordinat()"
            />
            <input
              type="button"
              value="Текущее состояние"
              class="modal__btn"
              @click="$emit('close')"
            />
            <!-- @click="submitCondition()" -->
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { actionTypes } from '@/store/modules/coordinats';
export default {
  name: 'AppModal',
  props: {
    item_device: {
      type: Object,
      default() {
        return {};
      },
    },
  },

  methods: {
    submitCoordinat() {
      this.$store
        .dispatch(actionTypes.getCoordinats, this.item_device.serial)
        .then(() => {
          this.$emit('close');
        })
        .catch((er) => {
          console.log('Ошибка', er);
        });
    },
    createNewCommand() {
      this.$emit('createNewCommand');
    },
  },
};
</script>

<style></style>
