<template>
  <div class="device">
    <div class="search">
      <div class="search-container">
        <input
          type="search"
          id="search-bar"
          name="search"
          placeholder="Серийный номер"
          v-model="serial"
          aria-label="Вводите"
          class="input-search"
        />
        <input
          type="search"
          id="search-bar"
          name="search"
          placeholder="Телефонный номер"
          v-model="phone"
          aria-label="Вводите"
          class="input-search"
        />
        <input
          type="search"
          id="search-bar"
          name="search"
          placeholder="IMEI устройства"
          v-model="imeiBlock"
          aria-label="Вводите"
          class="input-search"
        />
        <div class="search-wrap">
          <input
            type="button"
            value="Поиск"
            class="button btn-primary"
            @click="onSubmit()"
          />
        </div>
        <div class="search-wrap">
          <input
            type="button"
            value="Сбросить"
            class="button btn-primary"
            @click="deleteFilter()"
          />
        </div>
        <!-- @click="onChange(search)" -->
      </div>
    </div>
    <div class="table">
      <!-- {{ this.todosByTitle }} -->
      <table>
        <thead>
          <tr>
            <th></th>
            <th>Серийный номер</th>
            <th>Номер Сим-карты</th>
            <th>IMEI устройства</th>
            <th>IMEI СИМ-карты</th>
            <th>Пароль</th>
          </tr>
        </thead>

        <tbody>
          <!--    v-for="item in filter_device"-->
          <TableDeviceItem
            v-for="(item, index) in paginatedData"
            :key="item.id"
            v-bind:item_data="item"
            v-bind:idx="index"
            @modal-mini="modalMini"
          />
        </tbody>
      </table>
      <div class="table-block">
        <button :disabled="pageNumber === 0" @click="prevPage()">
          <svg
            aria-hidden="true"
            focusable="false"
            data-prefix="fas"
            data-icon="backward"
            class="svg-inline--fa fa-backward fa-w-16"
            role="img"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 512 512"
          >
            <path
              fill="currentColor"
              d="M11.5 280.6l192 160c20.6 17.2 52.5 2.8 52.5-24.6V96c0-27.4-31.9-41.8-52.5-24.6l-192 160c-15.3 12.8-15.3 36.4 0 49.2zm256 0l192 160c20.6 17.2 52.5 2.8 52.5-24.6V96c0-27.4-31.9-41.8-52.5-24.6l-192 160c-15.3 12.8-15.3 36.4 0 49.2z"
            ></path>
          </svg>
        </button>
        <p>{{ pageNumber + 1 }}</p>
        <button :disabled="pageNumber >= pageCount - 1" @click="nextPage()">
          <svg
            aria-hidden="true"
            focusable="false"
            data-prefix="fas"
            data-icon="forward"
            class="svg-inline--fa fa-forward fa-w-16"
            role="img"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 512 512"
          >
            <path
              fill="currentColor"
              d="M500.5 231.4l-192-160C287.9 54.3 256 68.6 256 96v320c0 27.4 31.9 41.8 52.5 24.6l192-160c15.3-12.8 15.3-36.4 0-49.2zm-256 0l-192-160C31.9 54.3 0 68.6 0 96v320c0 27.4 31.9 41.8 52.5 24.6l192-160c15.3-12.8 15.3-36.4 0-49.2z"
            ></path>
          </svg>
        </button>
      </div>
      <div class="table-error" v-if="paginatedData == 0">
        Данных не существует!
      </div>
      <modal-mini
        v-if="showModal"
        v-bind:item_device="this.item_device"
        @close="showModal = false"
        @createNewCommand="createNewCommand"
      />
      <modal-command
        v-if="showModalCommand"
        v-bind:item_device_command="this.item_device"
        @close="showModalCommand = false"
      />
    </div>
  </div>
</template>

<script>
import ModalMini from './Modal-mini.vue';
import TableDeviceItem from './Table-device-item.vue';
import { actionTypes } from '@/store/modules/devices';
import { mapState } from 'vuex';
import ModalCommand from './Modal-command.vue';
export default {
  name: 'AppTableDevice',
  data() {
    return {
      showModal: false,
      showModalCommand: false,
      serial: '',
      phone: '',
      imeiBlock: '',
      search_block: {},
      filter_device: [],
      pageNumber: 0,
      item_device: '',
    };
  },
  props: {
    size: {
      type: Number,
      required: false,
      default: 3,
    },
  },
  components: { TableDeviceItem, ModalMini, ModalCommand },
  computed: {
    ...mapState({
      isLoading: (state) => state.devices.isLoading,
      allDevices: (state) => state.devices.allDevices,
      itemDevice: (state) => state.devices.itemDevice,
    }),
    paginatedData() {
      const start = this.pageNumber * this.size,
        end = start + this.size;
      if (this.itemDevice) {
        return this.itemDevice;
      } else return this.allDevices.slice(start, end);
    },
    pageCount() {
      let l = this.allDevices.length,
        s = this.size;
      return Math.ceil(l / s);
    },
  },
  methods: {
    modalMini(item) {
      this.item_device = '';
      this.item_device = item;
      this.showModal = true;
    },
    //сортировка по всем данным
    onSubmit() {
      this.search_block = {
        serial: this.serial,
        phone: this.phone,
        imeiBlock: this.imeiBlock,
      };
      console.log(this.search_block);

      this.$store
        .dispatch(actionTypes.postSearch, this.search_block)
        .then((response) => {
          console.log(response);
          this.search_block = {};
          this.serial = '';
          this.phone = '';
          this.imeiBlock = '';
        })
        .catch((er) => {
          console.log('Ошибка', er);
        });
      // this.filter_device = [];
      // if (this.serial_search) {
      //   return this.table.filter((el) => {
      //     if (el.serial.includes(this.serial_search) == true) {
      //       return this.filter_device.push(el);
      //     }
      //   });
      // } else if (this.phone_search) {
      //   return this.table.filter((el) => {
      //     if (el.SIM_serial.includes(this.phone_search) == true) {
      //       return this.filter_device.push(el);
      //     }
      //   });
      // } else if (this.IMEI_search) {
      //   return this.table.filter((el) => {
      //     if (el.IMEI_block.includes(this.IMEI_search) == true) {
      //       return this.filter_device.push(el);
      //     }
      //   });
      // } else if (
      //   !this.serial_search &&
      //   !this.phone_search &&
      //   !this.IMEI_search
      // ) {
      //   this.filter_device = this.table;
      // }
    },
    createNewCommand() {
      this.showModal = false;
      this.showModalCommand = true;
    },
    deleteFilter() {
      this.$store.dispatch(actionTypes.deleteSearch);
      this.serial_search = '';
      this.phone_search = '';
      this.IMEI_search = '';
    },
    nextPage() {
      this.pageNumber++;
      console.log('next', this.pageNumber);
    },
    prevPage() {
      this.pageNumber--;
      console.log('page', this.pageNumber);
    },
  },

  mounted() {
    this.$store
      .dispatch(actionTypes.getAllDevices)
      .then((response) => {
        console.log(response);
      })
      .catch((er) => {
        console.log('Ошибка', er);
      });
  },
};
</script>
