<template>
  <transition name="modal">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container command">
          <div class="modal-header">
            <slot name="header">
              <p class="modal-header__title">
                Команда для: {{ item_device_command.serial }}
              </p>
            </slot>
          </div>
          <div class="modal-input">
            <input
              type="search"
              id="search-bar-command"
              name="search"
              placeholder="Команда"
              v-model="command"
              aria-label="Вводите"
              class="input-search"
            />
          </div>
          <div class="modal-body-command">
            <input
              type="button"
              value="Отправить"
              class="modal__btn-command"
              @click="submitCoordinat()"
            />
            <input
              type="button"
              value="Отмена"
              class="modal__btn-command"
              @click="$emit('close')"
            />
            <!-- @click="submitCondition()" -->
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { actionTypes } from '@/store/modules/devices';
export default {
  name: 'AppModalCommand',
  data() {
    return {
      command: '',
      messageType: 41,
      dataCommand: {},
    };
  },
  props: {
    item_device_command: {
      type: Object,
      default() {
        return {};
      },
    },
  },

  methods: {
    submitCoordinat() {
      console.log(this.dataCommand);
      this.dataCommand = {
        serial: this.item_device_command.serial,
        messageType: this.messageType,
        password: this.item_device_command.password,
        message: this.command,
      };
      // this.dataCommand.push({
      //   serial: this.item_device_command.serial,
      //   messageType: this.messageType,
      //   password: this.item_device_command.password,
      //   message: this.command,
      // });
      this.$store
        .dispatch(actionTypes.postCommand, this.dataCommand)
        .then(() => {
          this.$emit('close');
        })
        .catch((er) => {
          console.log('Ошибка', er);
        });
    },
  },
};
</script>

