import CoordinatsAPi from '@/api/coordinats.js';
// import { getItem } from '@/helpers/persistanceStorage';

const state = {
  isLoading: false, //ожидание запроса
  validationErrors: null, //ошибки
  coordinats: [],
};

export const mutationTypes = {
  CoordinatsStart: '[coordinats] CoordinatssStart',
  CoordinatsSuccess: '[coordinats] CoordinatsSuccess',
  CoordinatsFailure: '[coordinats] CoordinatsFailure',
};
export const actionTypes = {
  getCoordinats: '[coordinats] coordinats',
};

const mutations = {
  [mutationTypes.CoordinatsStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.CoordinatsSuccess](state, payload) {
    state.isLoading = false;
    state.coordinats = payload.data;
  },
  [mutationTypes.CoordinatsFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
};
const actions = {
  [actionTypes.getCoordinats](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.CoordinatsStart);
      CoordinatsAPi.coordinats(credentials)
        .then((response) => {
          context.commit(mutationTypes.CoordinatsSuccess, response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.CoordinatsFailure, resp);
          console.log('ошибка', resp);
        });
    });
  },
};
export default {
  state,
  mutations,
  actions,
};
